import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import Select from 'react-select'
import axios from 'axios'
import { selectThemeColors } from '@utils'
import { Card, CardHeader, CardBody, CardTitle, Table, FormGroup, Label, Input, Row, Col, Spinner } from 'reactstrap'

const tingkat = [
  { value: 'X', label: 'X' },
  { value: 'XI', label: 'XI' },
  { value: 'XII', label: 'XII' }
]

const kelas1 = [
  { value: '1', label: '1' },
  { value: '2', label: '2' },
  { value: '3', label: '3' },
  { value: '4', label: '4' },
  { value: '5', label: '5' },
  { value: '6', label: '6' },
  { value: '7', label: '7' },
  { value: '8', label: '8' },
  { value: '9', label: '9' },
  { value: '10', label: '10' },
  { value: '11', label: '11' },
  { value: '12', label: '12' }
]

const DataSiswa = () => {
  const user = localStorage.getItem('user')
  const [getTingkat, setTingkat] = useState('')
  const [getKelas, setKelas] = useState('')
  const [getSearch, setSearch] = useState('')
  const [loading, setLoading] = useState(false)
  const [data, setData] = useState(null)

  const history = useHistory()

  const pilihTingkat = (event) => {
    setTingkat(event.value)
  }

  const tampil = (event) => {
    setKelas(event.value)
  }

  const cari = (event) => {
    setSearch(event.value)
  }

  useEffect(() => {
    if (user === null) {
      history.push("/login")
    }

    if (getKelas) {
      setLoading(true)
      axios.post("https://smaalfacentauri.com/apis/sma.php", {
        act: 'getsiswa',
        token: process.env.REACT_APP_BEARER_TOKEN,
        tingkat: getTingkat,
        kelas: getKelas,
        cari: getSearch
      })
        .then(response => {
          setData(response.data)
          console.log(getKelas)
          console.log(response.data)
          setLoading(false)
        })
        .catch(error => {
          console.log(error)
        })
    }
  }, [getKelas, getSearch])

  return (
    <div>
      <Card>
        <CardBody>
          <Row>
            <Col className='mb-1' md='4' sm='12'>
              <Label>Pilih Tingkat</Label>
              <Select
                theme={selectThemeColors}
                className='react-select'
                classNamePrefix='select'
                options={tingkat}
                onChange={pilihTingkat}
                isClearable={false}
              />
            </Col>

            {getTingkat === 'X' && (
              <Col className='mb-1' md='4' sm='12'>
                <Label>Pilih Kelas</Label>
                <Select
                  theme={selectThemeColors}
                  className='react-select'
                  classNamePrefix='select'
                  options={kelas1}
                  onChange={tampil}
                  isClearable={false}
                />
              </Col>
            )}
          </Row>
        </CardBody>
      </Card>

      <Card>
        {loading && (
          <div className='d-flex justify-content-center my-1'>
            <Spinner color='primary' />
          </div>
        )}
        {data && (
          <div>
            <CardHeader>
              <CardTitle><b>Data Siswa</b></CardTitle>
            </CardHeader>
            <CardBody>
              <CardTitle tag='h4'>
                <Row style={{ float: 'right' }}>
                  <Col md='12' sm='12'>
                    <FormGroup className='form-label-group'>
                      <Input type='text' value={getSearch} id='floatingInput' onChange={cari} placeholder='Pencarian Siswa' />
                      <Label for='floatingInput'>{getSearch}</Label>
                    </FormGroup>
                  </Col>
                </Row>
              </CardTitle>
              <Table responsive>
                <thead>
                  <tr>
                    <th>No</th>
                    <th>Nama</th>
                    <th>NIS / NISN</th>
                    <th>PIN Siswa</th>
                    <th>PIN Ortu</th>
                    <th>Alamat</th>
                  </tr>
                </thead>
                <tbody>
                  {data.map((item, index) => (
                    <tr key={index}>
                      <td>{index + 1}</td>
                      <td>{item[4]}</td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                    </tr>
                  ))}
                </tbody>
              </Table>
            </CardBody>
          </div>
        )}
      </Card>
    </div>
  )
}

export default DataSiswa